import utils from "common/utils.js"

import refuse from "./child/refuse"
export default {
	data() {
		return {
			bottom:0,
			iphonex:false,
			current:0,
			isClose:0,
			options:[
				'全部',
				'待处理',
				'已接受',
				'已拒绝',
				'已完成',
			]
		}
	},
	components:{
		refuse
	},
	onLoad() {

	},
	onShow(){
		this.bottom = utils.bottom	
		this.iphonex = utils.iphonex
	},
	methods: {
		changeOption(i){	
			this.current = i;
		},
		isRefuse(){	//拒绝陪练
			this.isClose = 1;
		},
		closeRefuse(){
			this.isClose = 0;
		}
	}
}
